import React, { Component } from "react";
import { Light as SyntaxHighlighter } from "react-syntax-highlighter";
import js from "highlight.js/lib/languages/javascript";
import { createGlobalStyle } from "styled-components";
import Content from "../components-styled/Content";
import ContentCenter from "../components-styled/ContentCenter";
import GitHubImage from "../components-styled/GitHubImage";
import Header from "../components-styled/Header";
import Input from "../components-styled/Input";
import InputContainer from "../components-styled/InputContainer";
import Link from "../components-styled/Link";
import Title from "../components-styled/Title";
import QRCode from "react-qr-code";

const GlobalStyle = createGlobalStyle`
body{margin:0;padding:0;font-family:Fira Sans,Helvetica Neue,Apple SD Gothic Neo,Malgun Gothic,Segoe UI,sans-serif;font-weight:200;}
`;

SyntaxHighlighter.registerLanguage("javascript", js);

class App extends Component {
  state = {
    value: "www.weapp.se!",
  };

  onImageCownload = () => {
    const svg = document.getElementById("QRCode");
    const svgData = new XMLSerializer().serializeToString(svg);
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    const img = new Image();
    img.onload = () => {
      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0);
      const pngFile = canvas.toDataURL("image/png");
      const downloadLink = document.createElement("a");
      downloadLink.download = "QRCode";
      downloadLink.href = `${pngFile}`;
      downloadLink.click();
    };
    img.src = `data:image/svg+xml;base64,${btoa(svgData)}`;
  };

  onValueChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    const { value } = this.state;
    const repositoryLink = "https://www.weapp.se";
    return (
      <div
        style={{
          backgroundImage: "https://res.cloudinary.com/alvar-aronija/image/upload/v1681896920/weapp_h92oc9.webp",
        }}
      >
        <GlobalStyle />
        <Link href={repositoryLink}>
          <div style={{ height: 40, width: 40, overflow: "hidden" }}>
            <GitHubImage
              alt="We App"
              src="https://res.cloudinary.com/alvar-aronija/image/upload/v1681896920/weapp_h92oc9.webp"
            />
          </div>
        </Link>
        <Header>
          <Title>Maja's QR Code Generator</Title>
        </Header>
        <Content>
          <ContentCenter>
            <div>
              <QRCode id="QRCode" title="Custom Title" value={value} />
            </div>
            <InputContainer>
              <Input type="text" value={value} onChange={this.onValueChange} />
              <div style={{ height: 32 }} />
              <input type="button" value="Download QR" onClick={this.onImageCownload} />
            </InputContainer>
          </ContentCenter>
        </Content>
      </div>
    );
  }
}

export default App;
